// src/utils/assetUtils.js
export const getAssetPath = (path) => {
  // Remove any leading slash to prevent double slashes
  const cleanPath = path.startsWith('/') ? path.slice(1) : path;

  // In development, we want to serve from root
  if (process.env.NODE_ENV === 'development') {
    return `/${cleanPath}`;
  }

  // In production, we need the repository name
  return `/ashiq.github.io/${cleanPath}`;  // Match your repo name exactly
};