// src/ImageTest.jsx
import React from 'react';
import { getAssetPath } from './utils/assetUtils';

const ImageTest = () => {
  // Test with a simple logo we know exists
  const testImage = getAssetPath('logo/uottawa-logo.jpg');

  return (
    <div className="p-4">
      <p className="text-white mb-2">Development Mode: {process.env.NODE_ENV}</p>
      <p className="text-white mb-2">Public URL: {process.env.PUBLIC_URL}</p>
      <p className="text-white mb-2">Image path: {testImage}</p>
      <img
        src={testImage}
        alt="Test"
        className="w-32 h-32 object-contain bg-white/10 rounded-lg p-2"
        onError={(e) => {
          console.error('Image failed to load:', {
            src: e.target.src,
            error: e
          });
        }}
        onLoad={(e) => {
          console.log('Image loaded successfully:', {
            src: e.target.src,
            naturalWidth: e.target.naturalWidth,
            naturalHeight: e.target.naturalHeight
          });
        }}
      />
    </div>
  );
};

export default ImageTest;